import React from "react";
import Form from "../components/Form";
import { useMutation } from "react-apollo-hooks";
import * as Yup from "yup";
import { Segment, Divider, Header } from "semantic-ui-react";
import { REGISTER } from "../mutations/index";

const RegisterSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "Too Short!")
    .max(20, "Too Long!")
    .required("Required"),
  email: Yup.string()
    .email("Invalid email")
    .required("Required"),
  password: Yup.string()
    .min(8, "Too Short!")
    .max(30, "Too Long!")
    .required("Required")
});

const Register = props => {
  const addNewUser = useMutation(REGISTER);

  const onSubmit = async (values, actions) => {
    try {
      const { data } = await addNewUser({ variables: { data: values } });
      localStorage.setItem("token", data.register.token);
      props.history.push("/secret");
    } catch (error) {
      actions.setFieldError("email", "Email is in use please try another one");
      actions.setSubmitting(false);
    }
  };

  return (
    <div className="frm">
      <Segment stacked>
        <Divider horizontal>
          <Header><em>Register</em></Header>
        </Divider>
        <Form
          showUserName={true}
          initialValues={{ name: "", email: "", password: "" }}
          validationSchema={RegisterSchema}
          onSubmit={onSubmit}
        />
      </Segment>
    </div>
  );
};

export default Register;
