import React from "react";
import Form from "../components/Form";
import { useMutation } from "react-apollo-hooks";
import * as Yup from "yup";
import { Segment, Divider, Header } from "semantic-ui-react";
import {LOGIN} from '../mutations/index'

const LoginSchema = Yup.object().shape({
  email: Yup.string()
    .email("Invalid email")
    .required("Required"),
  password: Yup.string()
    .min(8, "Too Short!")
    .max(30, "Too Long!")
    .required("Required")
});

const Login = props => {
  const loginUser = useMutation(LOGIN);

  const onSubmit = async (values, actions) => {
    try {
      const { data } = await loginUser({ variables: { data: values } });
      localStorage.setItem("token", data.login.token);
      props.history.push("/secret");
    } catch (error) {
      const msg = error.message.split(":")[1];
      const isEmailError = msg.toLowerCase().includes("email");
      actions.setFieldError(isEmailError ? "email" : "password", msg);
      actions.setSubmitting(false);
    }
  };
  
  return (
    <div className="frm">
      <Segment stacked>
        <Divider horizontal>
          <Header><em>Login</em></Header>
        </Divider>
        <Form
          showUserName={false}
          initialValues={{ email: "", password: "" }}
          validationSchema={LoginSchema}
          onSubmit={onSubmit}
          showForgotPasswordLink={true}
        />
      </Segment>
    </div>
  );
};

export default Login;
